:root {
  --dropdown-length: 1.5s;
}

.welcomeMessage {
  width: fit-content;
  text-align: center;
  margin: 0 auto;
  margin-top: 100px;
  animation: dropdown var(--dropdown-length) cubic-bezier(0,0,0.35,2);
}

@media (min-height: 660px) {
  .welcomeMessage {
    margin-top: 200px;
  }
}

.stacks {
  margin-top: 50px;
}

.stack {
  display: inline-block;
  animation: stack-fly-in 0.5s ease-out;
  animation-fill-mode: backwards;
  animation-delay: var(--dropdown-length);
  margin-left: 5px;
}

.stack:nth-child(2) {
  animation-delay: calc(var(--dropdown-length) + 0.2s);
}
.stack:nth-child(3) {
  animation-delay: calc(var(--dropdown-length) + 0.4s);
}

.stack:nth-child(4) {
  animation-delay: calc(var(--dropdown-length) + 0.6s);
}

.stack:nth-child(5) {
  animation-delay: calc(var(--dropdown-length) + 0.8s);
}

@media (max-width: 1082px) {
  .welcomeMessage {
    width: 1082px;
  }
}

@keyframes dropdown {
  0%{
    transform: translateY(-400px);
  }

  80% {
    transform: translateY(0);
  }

  90% {
    transform: translateY(-15px);
  }

  100%{
    transform: translateY(0);
  }
}

@keyframes stack-fly-in {
  0%{
    transform: translateX(1000px);
    opacity: 0;
  }

  100%{
    transform: translateX(0);
  }
}

@keyframes hide-horizontal-scrollbars {
  to, from {
    overflow-x: hidden;
  }
}
