.card {
  display: flex;
  justify-content: space-between;
  height: 200px;
  width: 1000px;
  background-color: rgb(233, 233, 233);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  margin: 2em auto;
  border-radius: 12px;
}

li {
  list-style: none;
}

.projectDetails {
  position: relative;
}

.projectName {
  margin: 1em;
}

.projectName a {
  color: black;
  text-decoration: none;
}

.projectName a:hover {
  color: rgb(117, 117, 117);
}


.projectDescription {
  margin: 1em;
}

.projectStack {
  position: absolute;
  bottom: 0px;
  margin: 1em;
}

.projectStackList {
  display: inline;
  font-style: italic;
}

.imageDiv {
  display: flex;
  align-items: center;
  padding: 0.5em;
  max-width: 30%;
}

.imageDiv img {
  max-width: 100%;
  border-radius: 0.5em;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
}

.imageDiv img:hover {
  transform: scale(1.005);
  box-shadow: 0 2px 8px rgba(0, 0, 0.1, 0.25);

}