.nav{
  position: sticky;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  background-color: rgb(255, 255, 255, 0.95);
  width: 100%;
  height: 100px;
  z-index: 10;
}

@media (max-width: 1082px) {
  .nav {
    width: 1082px;
  }
}

.nav ul {
  display: flex;
  width: 20em;
  justify-content: center;
  align-items: center;
  padding: 0;
}

.nav li {
  list-style: none;
  margin: 0 1em;
}

.link {
  display: flex;
  margin: 0 1em;
  color: black;
  text-decoration: none;
  text-align: center;
}

h2 .link:hover {
  transform: scale(1.1);
}

.titleDiv {
  display: flex;
  width: 20em;
  justify-content:center;
  align-items: center;
}

.title:hover {
  transform: scale(1.05);
}

.pageTitle {
  display: flex;
  margin: 0 1em;
  align-items: center;
}

.active{
  color: grey;
  text-decoration: none;
}